.Header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 0px;
    border-color: var(--neon-green) !important;
    border-bottom: 3px solid var(--neon-green);
    padding: 8px;
    margin-bottom: 8px;
}

h1 {
    font-family: 'Press Start 2P', system-ui;
    font-size: 24px;
    color: var(--neon-green);
}

.Header svg {
    cursor: pointer;
    color: var(--neon-green);
}

.Header svg:hover {
    color: var(--neon-greenGlow);
}